@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  display: flex;
}

img {
  width: 100%;
  display: block;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  color: #000;
  cursor: pointer;
  border-bottom: 1px solid #000;
  text-decoration: none;
}

.footer {
  min-width: 100vw;
  color: #545454;
  text-align: center;
  background-color: #f7f7f7;
  align-items: center;
  padding: 42px 36px 42px 44px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

@media screen and (min-width: 768px) {
  .footer {
    width: 768px;
    font-size: 16px;
    line-height: 19px;
  }
}

@media screen and (min-width: 1024px) {
  .footer {
    width: 1024px;
  }
}

.container {
  min-width: 320px;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.gallery {
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  margin-bottom: 40px;
  list-style: none;
  display: grid;
}

.movie-card {
  width: 280px;
  height: 443px;
  box-sizing: content-box;
  justify-content: center;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
}

.movie-card:hover {
  cursor: pointer;
  transition: all .3s;
  transform: scale(1.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

img {
  height: 402px;
  object-fit: cover;
  border-radius: 5px;
}

.movie-card__info {
  align-items: left;
  text-align: left;
  vertical-align: top;
  width: 100%;
  height: 31px;
  color: #ff6b08;
  flex-direction: column;
  padding-top: 10px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.16em;
  display: flex;
}

.movie-card__title {
  text-transform: uppercase;
  color: #000;
  line-height: 1.16em;
}

.movie-card__title:after {
  content: "\a ";
  white-space: pre;
}

@media screen and (min-width: 768px) {
  .container {
    padding-left: 75px;
    padding-right: 75px;
  }

  .gallery {
    flex-flow: wrap;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-top: 60px;
    margin-bottom: 50px;
    list-style: none;
    display: grid;
  }

  .movie-card {
    width: 294px;
    height: 440px;
  }

  img {
    height: 398px;
  }

  .movie-card__info {
    height: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    padding-left: 71px;
    padding-right: 71px;
  }

  .gallery {
    grid-template-columns: repeat(3, 1fr);
  }

  .movie-card {
    width: 274px;
    height: 440px;
  }

  img {
    height: 398px;
  }
}

body {
  font-family: Roboto, sans-serif;
}

.open {
  width: 100px;
  height: 50px;
  color: #eee;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  background-color: #333;
  border-radius: 5%;
  font-size: 25px;
  line-height: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 1px 1px #222;
}

.close {
  width: 25px;
  height: 25px;
  text-align: center;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  line-height: 25px;
  position: absolute;
  top: 6px;
  right: 12px;
}

.close-x {
  font-size: 27px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal {
  min-width: 250px;
  max-height: 90%;
  width: 90%;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  animation: zoom .5s;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

@keyframes zoom {
  from {
    transform: translate(-50%, -50%)scale(0);
  }

  to {
    transform: translate(-50%, -50%)scale(1);
  }
}

.hidden {
  display: none;
}

.blur {
  width: 100%;
  height: 100%;
  filter: blur(8px);
  background-color: #aaa;
  position: absolute;
}

.image {
  min-height: 115vw;
  height: 1024px;
  width: 100%;
  background: url("imageMovieMobile.93954a9c.jpg") 50% 100% / contain no-repeat;
  margin: 40px auto 0;
}

.text {
  width: 90%;
  margin: 20px auto 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
}

.MovieTitle {
  margin: 0 auto;
  font-size: 20px;
  line-height: 23px;
}

.MovieTitle__info {
  margin-top: 20px;
  display: flex;
}

.MovieTitle__names {
  color: #8c8c8c;
}

.MovieTitle__info p:not(:first-child) {
  margin-top: 10px;
}

.MovieTitle__values {
  margin-left: 40px;
  line-height: 14px;
}

.MovieTitle__values .colorV {
  color: #8c8c8c;
}

.MovieTitle__values .colorVote {
  color: #fff;
  background: #ff6b01;
  border-radius: 7px;
  padding: 5px 10px;
}

.MovieTitle__values .colorVotes {
  color: #000;
  background: #f7f7f7;
  border-radius: 7px;
  padding: 5px 10px;
}

.text h2 {
  margin-top: 20px;
  font-size: 12px;
}

.AboutMovie {
  margin-top: 10px;
  line-height: 20px;
}

.buttonsWQ {
  min-width: 260px;
  justify-content: center;
  gap: 20px;
  margin: 21px 0 41px;
  display: flex;
}

.buttonsWQ button {
  cursor: pointer;
  width: 120px;
  border-radius: 5px;
  padding: 5px 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 16px;
}

.watched {
  color: #fff;
  background-color: #ff6b01;
  border: none;
}

.queue {
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
}

@media (min-width: 768px) {
  .modal {
    height: 60%;
    max-width: 80%;
    flex-direction: row;
    align-items: start;
  }

  .image {
    min-height: 80%;
    height: 50vw;
    width: 45%;
    background-position: top;
    margin: 0 auto 40px;
    position: relative;
    top: 50px;
  }

  .text {
    width: 45%;
    margin: 0 auto;
    position: relative;
    top: 50px;
  }
}

@media (min-width: 1024px) {
  .modal {
    height: 50vw;
    max-height: 827px;
    width: 80%;
    max-width: 1320px;
  }

  .image {
    height: 40vw;
    max-height: 650px;
    background: url("imageMovie.471bbe77.jpg") 100% 0 / contain no-repeat;
    margin: 0 0 40px;
  }

  .MovieTitle {
    font-size: 30px;
    line-height: 35px;
  }
}

